label {
  font-weight: var(--labelFontWeight);
}

[class$='--fullWidth'] {
  width: 100%;
}

.Button {
  cursor: pointer;
  font-weight: bold;
  border-radius: var(--borderRadius);
  border: 2px solid var(--primaryColor);
  padding: calc(var(--paddingSmall) - 2px) calc(var(--padding) - 2px);
  color: var(--fontColorInverted);
  box-shadow: var(--shadowDimsLight) var(--shadowColor);
}

.Button:disabled {
  cursor: not-allowed;
}

.Button:not(.Button--disableMaxContentWidth) {
  min-width: -moz-max-content;
  min-width: max-content;
}

.Button.Button--appearance-primary {
  border-color: var(--primaryColor);
  background-color: var(--primaryColor);
}

.Button.Button--appearance-primary:hover {
  border-color: var(--primaryColorHover);
  background-color: var(--primaryColorHover);
}

.Button.Button--appearance-primary:disabled {
  border-color: var(--primaryColorDisabled);
  background-color: var(--primaryColorDisabled);
}

.Button.Button--appearance-secondary {
  border-color: var(--secondaryColor);
  background-color: var(--secondaryColor);
}

.Button.Button--appearance-secondary:hover {
  border-color: var(--secondaryColorHover);
  background-color: var(--secondaryColorHover);
}

.Button.Button--appearance-secondary:disabled {
  border-color: var(--secondaryColorDisabled);
  background-color: var(--secondaryColorDisabled);
}

.Button.Button--appearance-danger {
  border-color: var(--dangerColor);
  background-color: var(--dangerColor);
}

.Button.Button--appearance-danger:hover {
  border-color: var(--dangerColorHover);
  background-color: var(--dangerColorHover);
}

.Button.Button--appearance-danger:disabled {
  border-color: var(--dangerColorDisabled);
  background-color: var(--dangerColorDisabled);
}

.Button.Button--variation-plain,
.Button.Button--variation-outline,
.Button.Button--variation-plainWithPadding {
  box-shadow: none;
}

.Button.Button--variation-outline,
.Button.Button--variation-outline:hover,
.Button.Button--variation-outline:disabled {
  background-color: var(--neutralColor);
}

.Button.Button--variation-plain,
.Button.Button--variation-plain:hover,
.Button.Button--variation-plain:disabled {
  background-color: transparent;
  border: none;
  padding: 0;
}

.Button.Button--variation-plainWithPadding,
.Button.Button--variation-plainWithPadding:disabled {
  background-color: transparent;
  border-color: transparent;
}

.Button.Button--variation-plainWithPadding:hover {
  background-color: var(--neutralColorHover);
  border-color: var(--neutralColorHover);
}

.Button.Button--variation-plain.Button--appearance-primary,
.Button.Button--variation-plainWithPadding.Button--appearance-primary,
.Button.Button--variation-outline.Button--appearance-primary {
  color: var(--primaryColor);
}

.Button.Button--variation-plain.Button--appearance-primary:hover,
.Button.Button--variation-plainWithPadding.Button--appearance-primary:hover,
.Button.Button--variation-outline.Button--appearance-primary:hover {
  color: var(--primaryColorHover);
}

.Button.Button--variation-plain.Button--appearance-primary:disabled,
.Button.Button--variation-plainWithPadding.Button--appearance-primary:disabled,
.Button.Button--variation-outline.Button--appearance-primary:disabled {
  color: var(--primaryColorDisabled);
}

.Button.Button--variation-plain.Button--appearance-secondary,
.Button.Button--variation-plainWithPadding.Button--appearance-secondary,
.Button.Button--variation-outline.Button--appearance-secondary {
  color: var(--secondaryColor);
}

.Button.Button--variation-plain.Button--appearance-secondary:hover,
.Button.Button--variation-plainWithPadding.Button--appearance-secondary:hover,
.Button.Button--variation-outline.Button--appearance-secondary:hover {
  color: var(--secondaryColorHover);
}

.Button.Button--variation-plain.Button--appearance-secondary:disabled,
.Button.Button--variation-plainWithPadding.Button--appearance-secondary:disabled,
.Button.Button--variation-outline.Button--appearance-secondary:disabled {
  color: var(--secondaryColorDisabled);
}

.Button.Button--variation-plain.Button--appearance-danger,
.Button.Button--variation-plainWithPadding.Button--appearance-danger,
.Button.Button--variation-outline.Button--appearance-danger {
  color: var(--dangerColor);
}

.Button.Button--variation-plain.Button--appearance-danger:hover,
.Button.Button--variation-plainWithPadding.Button--appearance-danger:hover,
.Button.Button--variation-outline.Button--appearance-danger:hover {
  color: var(--dangerColorHover);
}

.Button.Button--variation-plain.Button--appearance-danger:disabled,
.Button.Button--variation-plainWithPadding.Button--appearance-danger:disabled,
.Button.Button--variation-outline.Button--appearance-danger:disabled {
  color: var(--dangerColorDisabled);
}

.FormField {
}

.FormField.FormField--labelPosition-top
  .FormField-ElementContainer
  .FormField-Element {
  margin-top: var(--gapSmall);
}

.FormField.FormField--labelPosition-bottom
  .FormField-ElementContainer
  .FormField-Element {
  margin-bottom: var(--gapSmall);
}

.FormField.FormField--required .FormField-ElementContainer label::after {
  content: var(--requiredMarker);
  color: var(--dangerColor);
}

.FormField.FormField--labelPosition-left .FormField-ElementContainer,
.FormField.FormField--labelPosition-right .FormField-ElementContainer {
  display: flex;
}

.FormField.FormField--labelPosition-left .FormField-ElementContainer label {
  margin-right: var(--gapSmall);
}

.FormField.FormField--labelPosition-right .FormField-ElementContainer label {
  margin-left: var(--gapSmall);
}

.FormField.FormField--labelPosition-left .FormField-ElementContainer label,
.FormField.FormField--labelPosition-right .FormField-ElementContainer label {
  flex-grow: 1;
}

.FormField .FormField-Error {
  color: red;
  font-size: small;
  margin-top: var(--gapSmall);
}

.TextFieldElement {
  padding: var(--paddingSmall);
  border: var(--textFieldBorderWidth) solid var(--textFieldBorderColor);
  font: inherit;
  border-radius: var(--borderRadius);
}

.TextFieldElement:focus {
  border-color: var(--primaryColor);
  border-width: var(--textFieldBorderWidth);
  outline-color: var(--primaryColor);
}

.Checkbox {
  -webkit-appearance: none;
  background-color: var(--neutralColor);
  border: 1px solid var(--shadowColor);
  border-radius: var(--borderRadius);
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin: 0;
  width: 0;
  height: -moz-min-content;
  height: min-content;
}

.Checkbox,
.Checkbox--sizeVariant-normal {
  padding: calc(var(--checkboxWidth) / 2 - 1px);
}

.Checkbox--sizeVariant-large {
  padding: calc(var(--checkboxWidthLarge) / 2 - 1px);
}

.Checkbox:hover,
.Checkbox:checked:hover {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.Checkbox:checked {
  background-color: var(--primaryColor);
  border: 1px solid var(--primaryColor);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05),
    inset 0 15px 10px -12px rgba(255, 255, 255, 0.1);
  color: var(--fontColorInverted);
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3E%3Cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='m6 10 3 3 6-6'/%3E%3C/svg%3E");
}

.FormField.Checkbox-Container.FormField--labelPosition-right
  > .FormField-ElementContainer
  > .FormField-Element,
.FormField.Checkbox-Container.FormField--labelPosition-left
  > .FormField-ElementContainer
  > .FormField-Element {
  margin-top: 1px;
}

.Card {
  background-color: var(--neutralColor);
  box-shadow: var(--shadowDims) var(--shadowColor);
  padding: var(--padding);
  border-radius: var(--borderRadius);
}

.Card.Card--noPadding{
  padding: 0;
}

.Card.Card--elevation-low {
  box-shadow: var(--shadowDimsLight) var(--shadowColor);
}

.Card.Card--elevation-normal {
  box-shadow: var(--shadowDims) var(--shadowColor);
}

.Card.Card--elevation-high {
  box-shadow: var(--shadowDimsHeavy) var(--shadowColor);
}

.Popover .PopoverBody {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.Select {
  width: -moz-fit-content;
  width: fit-content;
}

.SelectAnchor {
  padding: var(--paddingSmall) var(--padding);
  cursor: pointer;
  border: 1px solid var(--textFieldBorderColor);
  box-sizing: border-box;
  border-radius: var(--borderRadius);
  display: flex;
  align-items: flex-start;
  min-width: -moz-fit-content;
  min-width: fit-content;
  background-color: var(--neutralColor);
}

.SelectAnchor:hover {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.SelectAnchor > .SelectAnchor-Value {
  flex-grow: 1;
}

.SelectAnchor .SelectAnchor-ExpandIcon {
  height: 18px;
}

.SelectAnchor .SelectAnchor-ExpandIcon svg {
  fill: var(--fontColorLighter);
  margin-top: -1px;
}

.Select > .Popover.Popover--open > .PopoverAnchor > .SelectAnchor {
  border: 1px solid transparent;
}

.Select > .Popover .PopoverBody > .Card {
  padding: 0;
  width: -moz-max-content;
  width: max-content;
}

.SelectOption {
  padding: var(--paddingSmall) var(--padding);
  background-color: var(--neutralColor);
  cursor: pointer;
}

.SelectOption.SelectOption--placeholder {
  color: var(--fontColorLighter);
}

.SelectAnchor.SelectAnchor--placeholder {
  color: var(--textFieldBorderColor);
}

.SelectOption:hover,
.SelectOption:focus {
  background-color: var(--neutralColorHover);
  outline: none;
}

.SelectOption.SelectOption--selected {
  background-color: var(--primaryColorBackground);
}

h1.Text,
h2.Text,
h3.Text,
h4.Text,
h5.Text,
h6.Text {
  margin: var(--headingMargin);
}

p.Text {
  margin: var(--paragraphMargin);
}

.Text.Text-InlineCode {
  font-size: 0.9em;
  border-radius: 2px;
  background: var(--codeBackgroundColor);
  padding: 2px;
  font-variant-ligatures: none;
  font-family: 'Fira Mono', 'Andale Mono', 'Consolas', monospace;
  letter-spacing: 0px;
  width: -moz-min-content;
  width: min-content;
  display: inline;
}

.Stack {
  display: flex;
  flex-direction: row;
}

.Stack.Stack--gap-nogap {
  gap: 0;
}

.Stack.Stack--gap-normal,
.Stack.Stack--gap-true {
  gap: var(--gap);
}

.Stack.Stack--gap-small {
  gap: var(--gapSmall);
}

.Stack.Stack--gap-smaller {
  gap: var(--gapSmaller);
}

.Stack.Stack--gap-large {
  gap: var(--gapLarge);
}

.Stack.Stack--gap-larger {
  gap: var(--gapLarger);
}

.Stack.Stack--vertical {
  flex-direction: column;
}

.Stack.Stack--wrapItems-wrap {
  flex-wrap: wrap;
}

.Stack.Stack--wrapItems-nowrap {
  flex-wrap: nowrap;
}

.Stack.Stack--wrapItems-wrap-reverse {
  flex-wrap: wrap-reverse;
}

.Stack.Stack--vertical.Stack--itemsVerticalAlignment-top,
.Stack.Stack--itemsHorizontalAlignment-left {
  justify-content: start;
}

.Stack.Stack--vertical.Stack--itemsVerticalAlignment-center,
.Stack.Stack--itemsHorizontalAlignment-center {
  justify-content: center;
}

.Stack.Stack--vertical.Stack--itemsVerticalAlignment-right,
.Stack.Stack--itemsHorizontalAlignment-right {
  justify-content: end;
}

.Stack.Stack--vertical.Stack--itemsVerticalAlignment-space-between,
.Stack.Stack--itemsHorizontalAlignment-space-between {
  justify-content: space-between;
}

.Stack.Stack--vertical.Stack--itemsVerticalAlignment-space-around,
.Stack.Stack--itemsHorizontalAlignment-space-around {
  justify-content: space-around;
}

.Stack.Stack--vertical.Stack--itemsVerticalAlignment-space-evenly,
.Stack.Stack--itemsHorizontalAlignment-space-evenly {
  justify-content: space-evenly;
}

.Stack.Stack--itemsVerticalAlignment-top,
.Stack.Stack--vertical.Stack--itemsHorizontalAlignment-left {
  align-items: start;
}

.Stack.Stack--itemsVerticalAlignment-center,
.Stack.Stack--vertical.Stack--itemsHorizontalAlignment-center {
  align-items: center;
}

.Stack.Stack--itemsVerticalAlignment-right,
.Stack.Stack--vertical.Stack--itemsHorizontalAlignment-right {
  align-items: end;
}

.Stack.Stack--itemsVerticalAlignment-space-between,
.Stack.Stack--vertical.Stack--itemsHorizontalAlignment-space-between {
  align-items: space-between;
}

.Stack.Stack--itemsVerticalAlignment-space-around,
.Stack.Stack--vertical.Stack--itemsHorizontalAlignment-space-around {
  align-items: space-around;
}

.Stack.Stack--itemsVerticalAlignment-space-evenly,
.Stack.Stack--vertical.Stack--itemsHorizontalAlignment-space-evenly {
  align-items: space-evenly;
}

.Stack > .StackItem--fill {
  flex-grow: 1;
}

.Table .TableCell {
  padding: var(--padding);
  text-align: left;
}

.Table,
.TableCell {
  border-collapse: collapse;
}

.Table.Table--hasRowDivider .TableCell {
  border-top: var(--textFieldBorderWidth) solid var(--shadowColor);
  border-bottom: var(--textFieldBorderWidth) solid var(--shadowColor);
}

.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableHead:first-child
  > .TableRow:first-child
  > .TableCell,
.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableRow:first-child
  > .TableCell,
.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableBody:first-child
  > .TableRow:first-child
  > .TableCell,
.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableFooter:first-child
  > .TableRow:first-child
  > .TableCell {
  border-top: none;
}

.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableHead:last-child
  > .TableRow:last-child
  > .TableCell,
.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableRow:last-child
  > .TableCell,
.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableBody:last-child
  > .TableRow:last-child
  > .TableCell,
.Table.Table--hasRowDivider:not(.Table--hasBodyBorder)
  > .TableFooter:last-child
  > .TableRow:last-child
  > .TableCell {
  border-bottom: none;
}

.Table.Table--hasColumnDivider .TableCell {
  border-left: var(--textFieldBorderWidth) solid var(--shadowColor);
  border-right: var(--textFieldBorderWidth) solid var(--shadowColor);
}

.Table.Table--hasColumnDivider:not(.Table--hasBodyBorder)
  .TableRow
  > .TableCell:first-child {
  border-left: none;
}

.Table.Table--hasColumnDivider:not(.Table--hasBodyBorder)
  .TableRow
  > .TableCell:last-child {
  border-right: none;
}

.Table.Table--headerCentered .TableCell--Head {
  text-align: center;
}
